.home_section1 {
  width: 100%;
  height: 220px;
  position: relative;
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.home_section1 .banniere {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 25px;
}
.home_section1 .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 60%;
  z-index: 1;
  border-radius: 25px;
}
.home_section1 p {
  font-size: 48px;
  font-weight: 700;
  color: white;
  z-index: 2;
}

@media only screen and (max-width: 767px) {
  .home_section1 {
    height: 111px;
  }

  .home_section1 p {
    font-size: 24px;
    width: 90%;
  }
}
